*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;outline:none;border:none;list-style-type:none;font-family:'Lato', serif}body{background-color:#FBFBFB}.dark-text{color:#25222A}.primaryPink-text{color:#F38181}

*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;outline:none;border:none;list-style-type:none;font-family:'Lato', serif}body{background-color:#FBFBFB}.dark-text{color:#25222A}.primaryPink-text{color:#F38181}.cc-navbar{background-color:#25222A;display:flex;flex-direction:row;padding:0 24px;justify-content:space-between;align-items:center;height:10vh;width:100%}.cc-navbar--logo{height:80%}.cc-navbar--logo_img{height:100%}.cc-navbar--links{display:flex;justify-content:space-between;align-items:center;width:15%;min-width:100px}.cc-navbar--link{color:#FBFBFB}

.cc-actionButton{display:flex;flex-direction:row;align-items:center;justify-content:space-around;border-radius:25px;width:20vw;height:8vh}.cc-actionButton--card{display:flex;flex-direction:column;justify-content:center;align-items:center;margin-right:15px}

.cc-errorMessage{color:red}

.cc-input_input{border:1px solid black}.cc-input--container{display:flex;flex-direction:column;justify-content:flex-start;align-items:center}

*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;outline:none;border:none;list-style-type:none;font-family:'Lato', serif}body{background-color:#FBFBFB}.dark-text{color:#25222A}.primaryPink-text{color:#F38181}.cc-card{display:flex;justify-content:center;align-items:center;flex-direction:column;border-radius:25px;box-shadow:0 0 16px rgba(0,0,0,0.16);background-color:#fbfbfb;min-width:100px;min-height:100px;height:80%;width:56%}

.cc-home{display:flex;flex-direction:row;justify-content:space-between;height:100%;width:100%}.cc-home--image-container{width:50%;height:90vh}.cc-home--image{width:100%;object-fit:contain}.cc-home--hero{display:flex;flex-direction:column;align-items:center;justify-content:center;width:50%;padding:0 24px}.cc-home--hero_title{font-size:48px}.cc-home--hero_action{width:80%;padding:48px 10px}.cc-home--hero_buttons{display:flex;justify-content:space-around;align-items:center;flex-direction:column;height:24vh}

